.TodoList {
    box-sizing: border-box;
    margin: 0 10px;
    padding: 10px;
    width: 500px;
    min-width: 300px;
    background-color: #fafbfc;
    border-radius: 10px;
    box-shadow: 0px 1px 3px 1px #ccc;
    color: #66748f;
    .topInput {
        width: 100%;
    }

    .middleLists {
        margin-top: 10px;
        max-height: 300px;
        overflow: auto;
    }

    .myFooter {
        margin-top: 10px;
    }
}