.List {
    height: 30px;
    display: flex;
    align-items: center;
    font-size: 14px;
    vertical-align: middle;
    label,input {
        vertical-align: middle;
    }
    background-color: #fff;
    border-bottom: 1px solid #e5e5e5;
    padding: 3px 0;
    label {
        margin-left: 6px;
    }
}