.Input {
    box-sizing: border-box;
    // border: 1px solid #e5e5e5;
    border: none;
    outline: 1px solid #e5e5e5;
    padding: 6px 6px;
    // box-shadow: 0px 1px 3px 1px #ccc;
    color: #66748f;
    background-color: #fff;
    font-weight: 700;
}

.Input::placeholder {
    color: rgba(125, 143, 178, 0.76)
}

.Input:focus{
    outline: 2px solid #3361ff;
    box-shadow: 0px 1px 3px 1px #1173a0;
}