.Footer {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    align-items: center;

    label {
        margin-left: 10px;
    }
    
    .rightButton {
        padding: 10px 10px;
        background: #3361ff;
        color: #fff;
        font-weight: 700;
        border: none;
        font-size: 12px;
        border-radius: 7px;
        box-shadow: 5px 5px 18px -4px #3361ff;
    }
}